
.basic {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}
.site-header {
  box-sizing: border-box;
  margin: 0 auto;
  background: #f60;
  width: 100%;
}
.site-header > nav {
  max-width: 800px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: auto;
  color: #fff;
}
.site-header > nav > img {
  width: 24px;
  margin-right: 30px;
}
.site-header > nav > p {
  margin-left: auto;
  font-size: 0.9em;
  color: #f5e7e7;
}
.nav__a {
  text-decoration: none;
  margin-right: 30px;
  font-weight: 500;
  color: rgba(255,255,255,0.8);
}
.nav__a:hover {
  color: #fff;
  font-weight: bold;
}
.nav__a.router-link-active {
  color: #fff;
  font-weight: bold;
}
.main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: auto;
}


