
.page-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #34495e;
  padding: 10px 0;
  background-color: #fff;
}
.page-nav__btn {
  cursor: pointer;
}
.page-nav__btn:first-child {
  margin-left: auto;
}
.page-nav__btn:last-child {
  margin-right: auto;
}
.page-nav__btn--disabled {
  color: #ccc;
  cursor: not-allowed;
}
.page-nav__current {
  margin: 10px;
}


