
.list__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
}
.list__item__score {
  color: #f60;
  font-size: 1.1em;
  font-weight: 700;
  margin-right: 20px;
  width: 50px;
}
.list__item__main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.list__item__title {
  line-height: 1.8rem;
}
.list__item__title > a {
  color: #34495e;
  text-decoration: none;
}
.list__item__title > span {
  padding-left: 10px;
  color: #828282;
  font-size: 0.8rem;
}
.list__item__meta {
  color: #828282;
  font-size: 0.9rem;
}
.list__item__meta > a {
  color: #828282;
  margin-right: 10px;
}
.list__item__meta > a:hover {
  color: #f60;
}
.list__item__meta > span {
  margin-right: 10px;
}


